@use 'sass:color';
@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0 , 0 , 0, .4);

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    p {
        margin: 0;
        font-size: 1.4rem;

        span {
            font-weight: 700;
            color: v.$naranjaOscuro;
        }
    }

    .cerrar-sesion {
        @include m.boton(color.adjust(v.$naranjaOscuro, $lightness: -20%), v.$blanco);
        & {
            font-size: 1.2rem;
            padding: 1rem;
            border-radius: .2rem;
        }
    }
} 